import { connect } from 'react-redux';
const initialState = {
    user: '',
    loggedIn:false,
    rememberMe: false,
    app: null,
    checkOutInfo: {
        amount: 0,
        currency: 'KES',
        title: 'Iphone 13',
        description: "We just alternated the location of the flashlight and the camera",
        accessKey: 'TmV0VmxpeDE2MzE4ODAyNzcxMTI=',
        subscription: false,
        number_of_days:0,
        start_date:0,
        escrow: false,
        callback_url: '',
        cancel_fine: 5,
        merchant_address: '',
        convAmnt: 0,
        fee: 0,
        feeConverted: 0,
        recepient: null
    }
},
    Connect = connect,
    reducer = (state = initialState, action) => {
        const { field, value, type } = action;
        if (type === 'setByField')
            return { ...state, [field]: value }
        else if (type === 'setByMultipleFields')
            return { ...state, ...value }
        if (type === 'logOut'){
            return initialState
        }
        return state
    },
    mapStateToProps = state => state,
    mapDispatchToProps = dispatch => ({
        setByField: (field, value) => dispatch({ type: 'setByField', field, value }),
        logOut: () => dispatch({ type: 'logOut' }),
        setByMultipleFields: (value) => dispatch({ type: 'setByMultipleFields', value })
    })
export {
    Connect,
    reducer,
    mapDispatchToProps,
    mapStateToProps
}