import React, { useState, useEffect } from 'react';
import { Box, Button } from '@material-ui/core';
import { Image, Error, Person } from "@material-ui/icons";
import Btn from '../components/button';
import useStyles from './styles';
import { Api, getDateTime, truncate } from '../shared/helper';
import Spinner from '../components/loader';
import Success from '../components/success';
import { useHistory } from 'react-router-dom';
import { Connect, mapDispatchToProps, mapStateToProps } from '../redux';
import LinesEllipsis from 'react-lines-ellipsis';
import invoke from 'react-native-webview-invoke/browser';

function Pay(props) {
    const [state, setState] = useState({
        loading: false,
        success: false,
        error: null,
        loadApp: false,
        loadAppError: null
    }),
        { loading, success, error, loadApp, loadAppError } = state,
        { user, setByMultipleFields, checkOutInfo, app, recepient } = props,
        { amount, currency, title, description, escrow, cancel_fine, merchant_address, subscription, number_of_days, start_date, callback_url, convAmnt, fee, feeConverted, redirectUrl } = checkOutInfo,
        classes = useStyles(),
        history = useHistory(),
        closeTab = () => {
            window.close();
            setByMultipleFields({ user: '', loggedIn: false, checkOutInfo: {}, app: null })
        },
        pay = () => {
            setState({ ...state, loading: true })
            let date = Date.now()
            Api('/apps_api/new_payment', {
                encrypted: true,
                action: "newAppPayment",
                amount,
                currency,
                merchant_address,
                userId: user._id,
                cancel_fine,
                escrow,
                callback_url,
                description,
                subscription,
                number_of_days,
                start_date,
                title,
                benkikoSdk: true,
                app,
                paymentInfo: {
                    ToId: recepient?._id,
                    ToAddress: recepient?.PublicKey,
                    Amount: convAmnt,
                    currency: {
                        Amount: parseFloat(amount),
                        symbol: currency,
                        fee: feeConverted
                    },
                    FromId: user._id,
                    FromAddress: user.PublicKey,
                    subscription,
                    escrow,
                    Username: user.Username,
                    Image: user.Image,
                    fee: parseFloat(fee),
                    userId: user._id,
                    date,
                    week: getDateTime(date).week,
                    year: getDateTime(date).year,
                    month: getDateTime(date).month,
                    createdAt: new Date(date).toString(),
                    appId: app?._id
                }
            }).then((resp) => {
                if (resp?.error)
                    setState({ ...state, loading: false, error: resp?.error });
                else {
                    const paymentResponse = invoke.bind('paymentResponse');
                    paymentResponse().then(res => alert(res)).catch(err => alert(err));
                    setState({ ...state, loading: false, success: true });
                    if (redirectUrl) {
                        window.location.href = redirectUrl
                    }
                    else
                        setTimeout(() => {
                            closeTab()
                        }, 2000)
                }
            }).catch(err => {
                alert(err)
                setState({ ...state, error, loading: false })
            })
        }



    useEffect(() => {
        return () => {
            closeTab()
        }
    }, [])
    return (
        <div className={classes.main}>
            <Box className={classes.payBg} boxShadow={3}>
                <div className={classes.paymentHeader}><div>Pay with Benkiko</div> <img className={classes.payImg} src="https://benkiko.s3.us-east-2.amazonaws.com/logo.png" /> </div>
                {loadApp ? <div className={classes.spinLoad}><Spinner /> </div> : loadAppError ? <div className={classes.loadAppError}>{loadAppError}</div> : success ? <Success amount={amount} currency={currency} /> :
                    <div className={classes.payBody}>
                        <div className={classes.header}>
                            {
                                <div className={classes.escrowTitle}>{escrow ? 'Escrow' : subscription ? 'Subscription' : 'Payment'}</div>
                            }
                            <div className={classes.logoHeader}>
                                {app?.icon ? <img
                                    className={classes.img}
                                    src={app?.icon}
                                    alt="Benkiko"
                                /> : <Image className={classes.appImage} />}
                                <div className={classes.payheaderTitle}>{app?.name}</div>
                            </div>
                        </div>
                        {escrow || subscription ? <div className={classes.escrowInfo}>
                            <div className={classes.descEscrowHeader}>
                                <Error className={classes.errorIcon} />
                                {cancel_fine ? <div className={classes.payDescription}> Cancellation fine {cancel_fine}% = ksh {parseFloat(amount * (checkOutInfo?.cancel_fine / 100))} </div> : null}
                                {subscription ? <div className={classes.payDescription}>Next payment on {getDateTime(start_date + (number_of_days * 24 * 3600 * 1000)).dbTime + ' at ' + getDateTime(start_date).time}</div> : null}
                            </div>
                        </div> : null}

                        <div className={classes.payTitle}>{truncate(title, 10)}</div>
                        <div className={classes.paymentDesc}>
                            <LinesEllipsis
                                className={classes.payDescription}
                                text={description}
                                maxLine='1'
                                ellipsis='...'
                                trimRight
                                basedOn='letters'
                            />
                        </div>

                        <div className={classes.userInfo}>
                            {recepient?.Image ? <img src={recepient?.Image} className={classes.userImg} /> : <div className={classes.imgIcon}>
                                <Person className={classes.userImgIcon} />
                            </div>}
                            <div className={classes.userName}>{recepient?.Username}</div>
                            <div className={classes.userPayInfo}>
                                <div className={classes.payInfoLabel}>Send</div>
                                <div className={classes.payInfoLabel}><span className={classes.currency}>{currency}</span>{amount?.toFixed(2)}</div>
                            </div>
                        </div>

                        <div className={classes.sbscriptionInfo}>
                            <div className={classes.subscription}>
                                <div className={classes.subscriptionTitle}>Fee:</div>
                                <div className={classes.paymentDetails}>
                                    <div className={classes.paymentTitle}>{currency}</div>
                                    <div className={classes.amnt}>{feeConverted?.toFixed(2)}</div>
                                </div>
                            </div>

                            <div className={classes.subscription}>
                                <div className={classes.subscriptionTitle}>Total:</div>
                                <div className={classes.paymentDetails}>
                                    <div className={classes.paymentTitle}>USDB</div>
                                    <div className={classes.amnt}>{convAmnt?.toFixed(2)}</div>
                                </div>
                            </div>
                        </div>
                        <div className={classes.btnWrapper}>
                            {loading ? <Spinner /> :
                                error ? <div className={classes.error}>{error}</div> : <Btn onClick={() => pay()}>Pay</Btn>}
                        </div>
                        <div className={classes.btnCancelWrapper}>
                            <Button className={classes.btnCancel} onClick={() => {
                                setByMultipleFields({ loggedIn: false })
                                history.push({ pathname: '/' })
                            }}>Change account</Button>
                        </div>
                        <div className={classes.btnCancelWrapper}>
                            <Button onClick={closeTab} className={classes.btnCancel}>Cancel and return to {app?.name}</Button>
                        </div>
                    </div>}
            </Box>
        </div>
    )
}

export default Connect(mapStateToProps, mapDispatchToProps)(Pay)
