import React from 'react'
import successImage from '../images/success.gif';
import useStyles from './styles'
const Success = (props) =>{
    const classes = useStyles(),
    { amount, currency } = props
    return (
        <div className={classes.successView}>
            <img src={successImage}  className={classes.successImg}/>
            <div className={classes.amnt}>{currency} {amount}</div>
           <div className={classes.successTxt}>Successfully Paid</div>
        </div>
    )
}

export default Success
